  z-index: 0;
  outline: none;
  position: relative;
}

.footer__glow:before {
  content: "";
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  opacity: 0;
  transition: opacity .3s ease-in-out cubic-bezier(.4, 0, .2, 1);
  background: linear-gradient(45deg, red, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, red) 0 0 / 400%;
  border-radius: 50px;
  animation: glowing 20s linear infinite;
  position: absolute;
  top: -2px;
  left: -2px;
}

.footer__glow:active {
  color: #f8d24e;
}

.footer__glow:active:after {
  background: none;
}

.footer__glow:hover:before {
  opacity: 1;
}

.footer__glow:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #f8d24e;
  border-radius: 50px;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

